import React, { useState, createContext , useContext } from 'react';
import { explorerData } from '../constants/processData';

const AppContext = createContext();


export const useAppContext = () => useContext(AppContext);


function Appcontext({children}) {
    const [mainTreeData, setMainTreeData] = useState(explorerData);
    const [currentProduct , setCurrentProduct] = useState(null);
  
    const updateMainTreeData = (newData) => {
        setMainTreeData(newData);
    };

    const updateCurrentProduct = (newData) => {
      setCurrentProduct(newData);
  };
  
    return (
      <AppContext.Provider value={{ mainTreeData, updateMainTreeData , currentProduct, updateCurrentProduct }}>
        {children}
      </AppContext.Provider>
    );
  }

export default Appcontext;