import React, { useState } from "react";
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';
import Divider from '@mui/material/Divider';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import CustomTreeView from "../CustomTreeView/CustomTreeView.jsx";
import { sideBarData } from "../../constants/sidebarStageData.js";
import "./SideBar.css";
const drawerWidth = 300;

const Sidebar = () => {
  const [searchText, setSearchText] = useState("");
  const [selectedTab, setSelectedTab] = useState(1);

  const handleSelectedTabChange = (event) => {
    setSelectedTab(Number(event.target.value));
  };

  const handleDrag = (e, innerItem, id) => {
    const customData = {
      name: innerItem,
      type: id
    };
    switch (id) {
      case "ch-1":
        // Handle the drop action for type 1
        e.dataTransfer.setData("flowChartDragEvent", JSON.stringify(customData));
        break;
      case "ch-2":
        // Handle the drop action for type 2
        e.dataTransfer.setData("processOperationDragEvent", JSON.stringify(customData));
        break;
      case "ch-3":
        // Handle the drop action for type 3
        e.dataTransfer.setData("processActionsDragEvent", JSON.stringify(customData));
        break;
      default:
        // Handle the drop action for other types or no type
        console.log("Dropped type:", id);
        break;
    }
  };


  return (
    <div className="sidebar-container">
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <Drawer
          variant="permanent"
          sx={{
            width: drawerWidth,
            flexShrink: 0,
            [`& .MuiDrawer-paper`]: { width: drawerWidth, boxSizing: 'border-box' },
          }}
        >
          <Toolbar />

          <Box backgroundColor="#FEFFFF" sx={{ overflow: 'auto' }}>
            <div className="sidebar-tab-selection">
              <FormControl>
                {/* <FormLabel id="demo-row-radio-buttons-group-label">Gender</FormLabel> */}
                <RadioGroup
                  row
                  aria-labelledby="demo-row-radio-buttons-group-label"
                  name="row-radio-buttons-group"
                  value={selectedTab}
                  onChange={handleSelectedTabChange}
                >
                  <FormControlLabel value="1" control={<Radio sx={{
                    color: '#4E9F3D',
                    '&.Mui-checked': {
                      color: '#4E9F3D',
                    },
                  }} />} label="Catalogue" />
                  <FormControlLabel value="2" control={<Radio sx={{
                    color: '#4E9F3D',
                    '&.Mui-checked': {
                      color: '#4E9F3D',
                    },
                  }} />} label="Template" />
                </RadioGroup>
              </FormControl>
            </div>
            {/* <Divider /> */}
            {selectedTab === 1 ?
              <div>
                <div className="sidebar-search">
                  <TextField sx={{ width: '95%' }} placeholder="Search..."
                    value={searchText} onChange={(e) => setSearchText(e.target.value)} id="outlined-basic" label="Search" variant="outlined" />
                </div>
                <CustomTreeView treeData={sideBarData} searchValue={searchText} handleDrag={handleDrag} />
              </div> : <div>
                Template
              </div>}
          </Box>
        </Drawer>
      </Box>
    </div>



  );
};
export default Sidebar;
